import { SITE_NAP } from "@/config/siteConfig"
import { Icon } from "@/app/components/Icon"
import { Link } from "@/app/utils/Link"

async function getGithubStats(username: string) {
    try {
        const res = await fetch(`https://api.github.com/users/${username}`, {
            next: { revalidate: 3600 },
        })

        if (!res.ok) {
            // Fallback or error handling
            return null;
        }

        return res.json()
    } catch (err) {
        return null
    }
}

export const GitHubStats = async () => {
    // Extract username from URL "https://github.com/mahirpatel2005" -> "mahirpatel2005"
    const username = SITE_NAP.profiles.github.split("/").pop() || "mahirpatel2005"
    const stats = await getGithubStats(username)

    if (!stats) {
        return (
            <div className="rounded-2xl border border-gray-200 bg-white p-6 shadow-sm">
                <div className="flex items-center gap-4">
                    <Icon name="github" className="h-8 w-8 text-gray-700" />
                    <div>
                        <h3 className="font-semibold text-gray-900">GitHub</h3>
                        <p className="text-sm text-gray-500">Could not load stats.</p>
                    </div>
                </div>
                <Link href={SITE_NAP.profiles.github} className="mt-4 block text-sm font-medium text-blue-600 hover:underline">
                    View Profile
                </Link>
            </div>
        )
    }

    return (
        <div className="group relative overflow-hidden rounded-3xl border border-gray-200 bg-white p-8 shadow-sm transition-all hover:shadow-md">
            <div className="flex flex-col gap-6 sm:flex-row sm:items-center sm:justify-between">
                <div className="flex items-center gap-4">
                    <div className="rounded-full bg-gray-50 p-2">
                        <Icon name="github" className="h-10 w-10 text-gray-900" />
                    </div>
                    <div>
                        <h3 className="bg-gradient-to-br from-gray-900 to-gray-600 bg-clip-text text-2xl font-bold text-transparent">GitHub</h3>
                        <p className="text-gray-500">@mahirpatel2005</p>
                    </div>
                </div>
                <Link
                    href={SITE_NAP.profiles.github}
                    target="_blank"
                    className="rounded-full border border-gray-200 bg-gray-50 px-5 py-2 text-sm font-medium text-gray-700 transition-colors hover:bg-gray-100 hover:text-gray-900"
                >
                    View Profile
                </Link>
            </div>

            <div className="mt-8 grid grid-cols-2 gap-4 sm:grid-cols-4">
                <div className="rounded-2xl bg-gray-50 p-4 transition-colors group-hover:bg-gray-100/80">
                    <p className="text-sm font-medium text-gray-500">Repositories</p>
                    <p className="text-2xl font-bold text-gray-900">{stats.public_repos}</p>
                </div>
                <div className="rounded-2xl bg-gray-50 p-4 transition-colors group-hover:bg-gray-100/80">
                    <p className="text-sm font-medium text-gray-500">Followers</p>
                    <p className="text-2xl font-bold text-gray-900">{stats.followers}</p>
                </div>
                <div className="rounded-2xl bg-gray-50 p-4 transition-colors group-hover:bg-gray-100/80">
                    <p className="text-sm font-medium text-gray-500">Following</p>
                    <p className="text-2xl font-bold text-gray-900">{stats.following}</p>
                </div>
                <div className="rounded-2xl bg-gray-50 p-4 transition-colors group-hover:bg-gray-100/80">
                    <p className="text-sm font-medium text-gray-500">Gists</p>
                    <p className="text-2xl font-bold text-gray-900">{stats.public_gists}</p>
                </div>
            </div>

            {/* Background Decoration */}
            <div className="absolute -right-10 -top-10 h-32 w-32 rounded-full bg-gray-100 opacity-50 blur-3xl transition-all group-hover:bg-gray-200" />
        </div>
    )
}
